import React from "react";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 6px;
`;

const CompletionText = styled.div`
  font-size: 12px;
  margin-right: 10px;
`;

const CompletionProgressWrapper = styled.div`
  width: 100px;
  position: relative;
`;

const CompletionProgressBar = styled.div`
  width: 100%;
  height: 6px;
  background: rgba(255, 71, 115, 0.3);
  border-radius: 10px;
`;

const CompletionProgress = styled.div`
  position: absolute;
  width: ${({ progress }) => progress}%;
  height: 6px;
  background: rgba(255, 71, 115, 0.9);
  border-radius: 10px;
`;

function ProfileCompletion({ progress }) {
  return (
    <Container>
      <CompletionText>Profile Completed {progress}%</CompletionText>
      <CompletionProgressWrapper>
        <CompletionProgress progress={progress} />
        <CompletionProgressBar />
      </CompletionProgressWrapper>
    </Container>
  );
}

export default ProfileCompletion;
