import React from "react";
import styled from "styled-components";
import { ReactComponent as User } from "../../assets/Icon/user.svg";
import { ReactComponent as Camera } from "../../assets/Icon/camera.svg";
import { useFileSelect } from "../../hooks/useFileSelect";
import { useEffect } from "react";

const Container = styled.div`
  position: relative;
  width: 100%;
`;

const Label = styled.label`
  font-size: 12px;
`;

const PictureWrapper = styled.label`
  cursor: pointer;
  position: relative;
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  border-radius: 50%;
  background: rgba(255, 237, 241, 1);
  ${({ hasError }) => hasError && `border: 1px solid red`};
`;

const Picture = styled.img`
  height: 100%;
  width: 100%;
  object-fit: fill;
  border-radius: 50%;
`;

const CameraIcon = styled(Camera)`
  position: absolute;
  right: 0;
  bottom: 0;
`;

const FileBrowse = styled.input.attrs({ type: "file", id: "picture-upload" })`
  display: none;
`;

const Error = styled.label`
  color: red;
  font-size: 10px;
  position: absolute;
  bottom: 6px;
  right: 35px;
`;

function PictureUpload({ label, src, onChange, name, error }) {
  const [selectedFile, selectedSrc, isImage, handleFileChange] =
    useFileSelect();
  useEffect(() => {
    selectedFile && onChange(selectedFile);
  }, [selectedFile]);
  return (
    <Container>
      <Label>{label}</Label>
      <FileBrowse onChange={handleFileChange} name={name} />
      <PictureWrapper
        hasError={error}
        onChange={handleFileChange}
        htmlFor="picture-upload"
      >
        {selectedSrc || src ? (
          <Picture src={selectedSrc || src}></Picture>
        ) : (
          <User />
        )}
        <CameraIcon />
      </PictureWrapper>
      {error && <Error>{error}</Error>}
    </Container>
  );
}

export default PictureUpload;
