import React from "react";
import styled from "styled-components";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";

const Container = styled.div`
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
`;

const Label = styled.label`
  font-size: 12px;
  margin-bottom: 6px;
`;

const StyledDropdown = styled(Dropdown)`
  .Dropdown-control {
    padding: 12px 20px 12px 16px;
    background: #ffedf1;
    border: none;
    outline: none;
    border-radius: 0;
    font-size: 13.33px;
    line-height: 16px;
    :hover {
      box-shadow: none;
    }
  }
  .Dropdown-option.is-selected {
    background: #ff4773;
    color: white;
  }
  ${({ hasError }) => hasError && `border: 0.1px solid red;`}
`;

const Error = styled.label`
  color: red;
  font-size: 10px;
  position: absolute;
  bottom: -9px;
  right: 0;
`;

function DropdownReact({
  label,
  options,
  value,
  placeholder,
  onChange,
  name,
  error,
}) {
  const handleChange = (selected) => {
    onChange && onChange({ target: { ...selected, name } });
  };
  return (
    <Container>
      <Label>{label}</Label>
      <StyledDropdown
        hasError={error}
        options={options}
        onChange={handleChange}
        value={value}
        placeholder={placeholder}
      />
      {error && <Error>{error}</Error>}
    </Container>
  );
}

export default DropdownReact;
