import React from "react";
import styled from "styled-components";
import { Route } from "react-router-dom";
import Profiles from "./Profiles/Profiles";
import ProfileSetting from "./ProfileSetting/ProfileSetting";
import LeftMenu from "./LeftMenu/LeftMenu";
import Profile from "../Profile/Profile";
import ResetPassword from "../Login/ResetPassword";

const Container = styled.div`
  display: flex;
`;

const Body = styled.div`
  margin-left: 250px;
  width: 100%;
`;

const ProfileBody = styled.div`
  width: fit-content;
  margin-left: 20px;
`;

function Admin() {
  return (
    <Container>
      <LeftMenu />
      <Body>
        <Route exact path={`/admin/profiles`} component={Profiles} />
        <ProfileBody>
          <Route exact path={`/admin/profiles/:id`} component={Profile} />
          <Route exact path={`/admin/reset-password`} component={ResetPassword} />
        </ProfileBody>
        <Route path={`/admin/profile-setting`} component={ProfileSetting} />
      </Body>
    </Container>
  );
}

export default Admin;
