import styled from "styled-components";
import React from "react";

const Container = styled.div`
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 14px;
`;

const InputField = styled.input`
  padding: 12px 20px 12px 16px;
  background: #ffedf1;
  outline: none;
  border: none;
  :placeholder {
    color: rgba(38, 24, 10, 0.6);
  }
  ${({ hasError }) => hasError && `border: 0.1px solid red;`}
`;

const Label = styled.label`
  font-size: 12px;
  margin-bottom: 6px;
`;

const IconWrapper = styled.div`
  position: absolute;
  right: 5px;
  top: 32px;
`;

const Error = styled.label`
  color: red;
  font-size: 10px;
  position: absolute;
  bottom: -12px;
  right: 0;
`;

function Input({
  label,
  value,
  type,
  name,
  placeholder,
  icon,
  onChange,
  readOnly,
  error,
}) {
  return (
    <Container>
      <Label>{label}</Label>
      <InputField
        hasError={error}
        type={type}
        value={value}
        placeholder={placeholder}
        onChange={onChange}
        name={name}
        readOnly={Boolean(readOnly)}
      />
      {icon && <IconWrapper>{icon}</IconWrapper>}
      {error && <Error>{error}</Error>}
    </Container>
  );
}

export default Input;
