import { Link } from "react-router-dom";
import styled, { css } from "styled-components";

const PrimaryButtonStyle = css`
  background: #ff4773;
  border: none;
  outline: none;
  padding: 10px 25px;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
`;

const StyledPrimaryButton = styled.button`
  ${PrimaryButtonStyle};
  position: relative;
  margin: 10px 0;
  ${({ outline }) =>
    !outline &&
    `&:hover {
    transition: all 0.2s ease-in-out;
    background: #ff0944;
  }`}
  ${({ block }) => block && `width: 100%;`}
  ${({ outline }) =>
    outline &&
    `background: none;
    color: #ff4773;
    border: 1px solid #ff4773;`}
`;

export const PrimaryLinkButton = styled(Link)`
  ${PrimaryButtonStyle}
  &:hover {
    transition: all 0.2s ease-in-out;
    background: #ff0944;
  }
  text-decoration: none;
`;

export const PrimaryOutlineButton = styled.button`
  ${PrimaryButtonStyle}
  background: none;
  color: #ff4773;
  border: 1px solid #ff4773;
`;

export const PrimaryTextButton = styled.button`
  ${PrimaryButtonStyle}
  color: #ff4773;
  background: #ffffff;
`;

const Loader = styled.div`
  background: #ff4773;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  height: 100%;
  .loader,
  .loader:before,
  .loader:after {
    border-radius: 50%;
    width: 2.5em;
    height: 2.5em;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation: load7 1.8s infinite ease-in-out;
    animation: load7 1.8s infinite ease-in-out;
  }
  .loader {
    position: absolute;
    top: calc(50% - 10px);
    left: calc(50% - 4px);
    color: #ffffff;
    font-size: 3px;
    margin: auto;
    text-indent: -9999em;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  }
  .loader:before,
  .loader:after {
    content: "";
    position: absolute;
    top: 0;
  }
  .loader:before {
    left: -3.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }
  .loader:after {
    left: 3.5em;
  }
  @-webkit-keyframes load7 {
    0%,
    80%,
    100% {
      box-shadow: 0 2.5em 0 -1.3em;
    }
    40% {
      box-shadow: 0 2.5em 0 0;
    }
  }
  @keyframes load7 {
    0%,
    80%,
    100% {
      box-shadow: 0 2.5em 0 -1.3em;
    }
    40% {
      box-shadow: 0 2.5em 0 0;
    }
  }
`;

export function PrimaryButton({
  children,
  isLoading,
  onClick,
  block,
  outline,
}) {
  return (
    <StyledPrimaryButton onClick={onClick} block={block} outline={outline}>
      {children}
      {isLoading && (
        <Loader>
          <div class="loader"></div>
        </Loader>
      )}
    </StyledPrimaryButton>
  );
}
