import React, { useEffect } from "react";
import { Card } from "../../Common/components/Card/Card";
import styled from "styled-components";
import { PrimaryButton } from "../../Common/components/Buttons/PrimaryButton";
import { LinkText } from "../../Common/components/LinkText/LinkText";
import PictureUpload from "../../Common/components/FileUpload/PictureUpload";
import FileUpload from "../../Common/components/FileUpload/FileUpload";
import { connect } from "react-redux";

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const SignupButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const BackLink = styled(LinkText)`
  text-align: center;
  margin-top: 10px;
`;

function ProfileInformation({
  handleSubmit,
  handleBack,
  handleChange,
  isLoading,
  errors,
  setErrors,
}) {
  useEffect(() => {
    setErrors({});
  }, [setErrors]);
  return (
    <>
      <Card width={450}>
        <Title>Profile Information</Title>
        <InputWrapper>
          <PictureUpload
            error={errors.photo1}
            label={"Profile Image"}
            name="photo1"
            onChange={(file) => handleChange("photo1", file)}
            // src={
            //   "https://miro.medium.com/max/2400/1*mk1-6aYaf_Bes1E3Imhc0A.jpeg"
            // }
          />
          <FileUpload
            error={errors.horoscope}
            name="horoscope"
            onChange={(file) => handleChange("horoscope", file)}
            label={"Kundali"}
          />
          <SignupButton isLoading={isLoading} onClick={handleSubmit}>
            Finish And Submit
          </SignupButton>
          <BackLink onClick={handleBack}>Back</BackLink>
        </InputWrapper>
      </Card>
    </>
  );
}

const mapStateToProps = (state) => ({
  isLoading: state.registration.registering,
});

export default connect(mapStateToProps)(ProfileInformation);
