import React from "react";
import { Card } from "../../Common/components/Card/Card";
import Input from "../../Common/components/Input/Input";
import styled from "styled-components";
import { PrimaryButton } from "../../Common/components/Buttons/PrimaryButton";
import { LinkText } from "../../Common/components/LinkText/LinkText";
import { useEffect } from "react";

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const Row = styled.div`
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 180px 180px;
`;

const Text = styled.h3`
  text-align: center;
  font-weight: 400;
  font-size: 13px;
`;

const SignupButton = styled(PrimaryButton)`
  margin-top: 10px;
  width: 100%;
`;

function Credentials({
  values,
  handleChange,
  handleContinue,
  validateForm,
  errors,
  setErrors,
}) {
  useEffect(() => {
    setErrors({});
  }, [setErrors]);
  const gotoNextStep = async () => {
    const err = await validateForm();
    if (!(err.first_name || err.father_name || err.phone_no || err.password)) {
      handleContinue();
    }
  };
  return (
    <Card width={450}>
      <Title>Signup</Title>
      <InputWrapper>
        <Input
          label="Full Name"
          type="text"
          name="first_name"
          value={values.first_name}
          onChange={handleChange}
          placeholder="Enter your full name"
          error={errors.first_name}
        />
        <Input
          label="Fathers Name"
          type="text"
          name="father_name"
          value={values.father_name}
          onChange={handleChange}
          placeholder="Enter your fathers name"
          error={errors.father_name}
        />
        <Row>
          <Input
            label="Phone"
            value={values.phone_no}
            onChange={handleChange}
            type="text"
            name="phone_no"
            placeholder="Enter phone number"
            error={errors.phone_no}
          />
          <Input
            label="Password"
            type="password"
            name="password"
            onChange={handleChange}
            value={values.password}
            placeholder="Enter password"
            error={errors.password}
          />
        </Row>
        <SignupButton onClick={gotoNextStep}>Continue</SignupButton>
        <Text>
          Already a member? <LinkText to="/login">Login</LinkText>
        </Text>
      </InputWrapper>
    </Card>
  );
}

export default Credentials;
