import { userConstants } from "../constant";
import { userService } from "../service";

export const userActions = {
  login,
  logout,
  register,
  getUserList,
  approveProfile,
  rejectProfile,
  resetPassord,
};

function login(username, password, onSuccess, onFailure) {
  return (dispatch) => {
    dispatch(request({ username }));

    userService.login(username, password).then(
      (user) => {
        onSuccess(user);
        dispatch(success(user));
      },
      (error) => {
        onFailure(error);
        dispatch(failure(error.toString()));
      }
    );
  };

  function request(user) {
    return { type: userConstants.LOGIN_REQUEST, user };
  }
  function success(user) {
    return { type: userConstants.LOGIN_SUCCESS, user };
  }
  function failure(error) {
    return { type: userConstants.LOGIN_FAILURE, error };
  }
}

function logout(onSuccess, onFailure) {
  userService
    .logout()
    .then(() => {
      onSuccess();
    })
    .catch((err) => {
      onFailure(err);
    });
  return { type: userConstants.LOGOUT };
}

function register(user, onSuccess, onFailure) {
  return (dispatch) => {
    dispatch(request(user));

    userService.register(user).then(
      (user) => {
        onSuccess();
        dispatch(success(user));
      },
      (error) => {
        onFailure(error);
        dispatch(failure(error.toString()));
      }
    );
  };

  function request(user) {
    return { type: userConstants.REGISTER_REQUEST, user };
  }
  function success(user) {
    return { type: userConstants.REGISTER_SUCCESS, user };
  }
  function failure(error) {
    return { type: userConstants.REGISTER_FAILURE, error };
  }
}

function getUserList(filter) {
  return (dispatch) => {
    dispatch(request());

    userService.userList(filter).then(
      (users) => {
        dispatch(success(users));
      },
      (error) => {
        dispatch(failure(error.toString()));
      }
    );
  };

  function request() {
    return { type: userConstants.USERLIST_REQUEST };
  }
  function success(users) {
    return { type: userConstants.USERLIST_SUCCESS, users };
  }
  function failure(error) {
    return { type: userConstants.USERLIST_FAILURE, error };
  }
}

function approveProfile(userId, onSuccess) {
  return (dispatch) => {
    dispatch(request(userId));

    userService.approveProfile(userId).then(
      (res) => {
        onSuccess();
        dispatch(success(res));
      },
      (error) => {
        dispatch(failure(error.toString()));
      }
    );
  };

  function request(userId) {
    return { type: userConstants.APPROVE_REQUEST, userId };
  }
  function success(users) {
    return { type: userConstants.APPROVE_SUCCESS, users };
  }
  function failure(error) {
    return { type: userConstants.APPROVE_FAILURE, error };
  }
}

function rejectProfile(userId, onSuccess, onFailure) {
  return (dispatch) => {
    dispatch(request(userId));

    userService.rejectProfile(userId).then(
      (res) => {
        onSuccess();
        dispatch(success(res));
      },
      (error) => {
        onFailure(error);
        dispatch(failure(error.toString()));
      }
    );
  };

  function request(userId) {
    return { type: userConstants.REJECT_REQUEST, userId };
  }
  function success(users) {
    return { type: userConstants.REJECT_SUCCESS, users };
  }
  function failure(error) {
    return { type: userConstants.REJECT_FAILURE, error };
  }
}

function resetPassord(phone_no, password, onSuccess, onFailure) {
  return (dispatch) => {
    dispatch(request(phone_no));
    userService.resetPassword(phone_no, password).then(
      (res) => {
        onSuccess();
        dispatch(success(res));
      },
      (error) => {
        onFailure && onFailure(error);
        dispatch(failure(error.toString()));
      }
    );
  };

  function request(userId) {
    return { type: userConstants.REJECT_REQUEST, userId };
  }
  function success(users) {
    return { type: userConstants.REJECT_SUCCESS, users };
  }
  function failure(error) {
    return { type: userConstants.REJECT_FAILURE, error };
  }
}
