import React from "react";
import styled from "styled-components";
import Input from "../Common/components/Input/Input";
import Radio from "../Common/components/Radio/Radio";
import { withFormik } from "formik";
import { PrimaryButton } from "../Common/components/Buttons/PrimaryButton";
import { connect } from "react-redux";
import { userActions } from "../redux/Login/actions";

const Container = styled.div`
  width: 250px;
  position: fixed;
  padding: 20px;
  height: 100vh;
  -webkit-box-shadow: 2px 0px 58px rgba(255, 71, 115, 0.1);
  -moz-box-shadow: 2px 0px 58px rgba(255, 71, 115, 0.1);
  box-shadow: 2px 0px 58px rgba(255, 71, 115, 0.1);
`;

const Text = styled.h3`
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
`;

const Row = styled.div`
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 50% 50%;
`;

const ApplyButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const RELIGIONS = [
  { id: "hindu", value: "Hindu" },
  { id: "jain", value: "Jain" },
  { id: "muslim", value: "Muslim" },
];

function FilterSection({ values, handleChange, handleSubmit }) {
  return (
    <Container>
      <Text>Age</Text>
      <Row>
        <Input
          placeholder="Min"
          name="age_from"
          value={values.age_from}
          onChange={handleChange}
        />
        <Input
          placeholder="Max"
          name="age_to"
          value={values.age_to}
          onChange={handleChange}
        />
      </Row>
      <Text>Height</Text>
      <Row>
        <Input
          placeholder="Min"
          name="height_from"
          value={values.height_from}
          onChange={handleChange}
        />
        <Input
          placeholder="Max"
          name="height_to"
          value={values.height_to}
          onChange={handleChange}
        />
      </Row>
      <Text>Religion</Text>
      <Row>
        <Radio
          options={RELIGIONS}
          name="religion"
          selected={values.religion}
          onChange={handleChange}
        />
      </Row>
      <ApplyButton onClick={handleSubmit} block>
        Apply
      </ApplyButton>
    </Container>
  );
}

const FilterSectionFormik = withFormik({
  mapPropsToValues: () => ({
    filter: "",
  }),
  handleSubmit: (values, { props }) => {
    props.getUserList(values);
  },
})(FilterSection);

const mapDispatchToProps = {
  getUserList: userActions.getUserList,
};

export default connect(null, mapDispatchToProps)(FilterSectionFormik);
