import { withFormik } from "formik";
import React from "react";
import { connect, useSelector } from "react-redux";
import styled from "styled-components";
import { PrimaryButton } from "../Common/components/Buttons/PrimaryButton";
import { Card } from "../Common/components/Card/Card";
import Input from "../Common/components/Input/Input";
import { userActions } from "../redux/Login/actions";
import * as Yup from "yup";
import { toast } from "react-toastify";

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const LoginButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

function ResetPassword({ values, handleChange, handleSubmit, errors }) {
  const resetting = useSelector((state) => {
    console.log(state);
    return state.authentication.resetting;
  });
  return (
    <Card width={400}>
      <Title>Reset Password</Title>
      <InputWrapper>
        <Input
          label="Phone"
          type="text"
          name="phone_no"
          onChange={handleChange}
          value={values.phone_no}
          error={errors.phone_no}
          placeholder="Enter phone number"
        />
        <Input
          label="Password"
          type="password"
          name="password"
          value={values.password}
          onChange={handleChange}
          error={errors.password}
          placeholder="Enter password"
        />
        <LoginButton isLoading={resetting} onClick={handleSubmit}>
          Submit
        </LoginButton>
      </InputWrapper>
    </Card>
  );
}

const ValidationSchema = Yup.object().shape({
  phone_no: Yup.string()
    .required("Phone Number is required")
    .matches(/^[0-9]+$/, "Must be only digits")
    .min(10, "Must be exactly 10 digits")
    .max(10, "Must be exactly 10 digits"),
  password: Yup.string().required("Password is required"),
});

const ResetPasswordFormik = withFormik({
  mapPropsToValues: () => ({ phone_no: "", password: "" }),
  validationSchema: ValidationSchema,
  handleSubmit: (values, { props }) => {
    props.resetPassord(
      values.phone_no,
      values.password,
      () => {
        toast.success("Password reset successfully", {
          position: "bottom-right",
        });
        props.history.goBack();
      },
      (err) => {
        toast.error("Failed to reset password - " + err, {
          position: "bottom-right",
        });
      }
    );
  },
})(ResetPassword);

const mapDispatchToProps = {
  resetPassord: userActions.resetPassord,
};

export default connect(null, mapDispatchToProps)(ResetPasswordFormik);
