export const userService = {
  login,
  logout,
  register,
  userList,
  approveProfile,
  rejectProfile,
  resetPassword,
};

const BASE_URL = "http://3.13.196.252/management";

function login(phone_no, password) {
  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ phone_no, password }),
  };

  return fetch(`${BASE_URL}/login/`, requestOptions)
    .then(handleResponse)
    .then((user) => {
      // store user details and jwt token in local storage to keep user logged in between page refreshes
      localStorage.setItem("user", JSON.stringify(user));
      localStorage.setItem("user_token", JSON.stringify(user.user_token));

      return user;
    });
}

function logout() {
  // remove user from local storage to log user out
  const token = `${JSON.parse(localStorage.getItem("user_token"))}`;
  localStorage.removeItem("user");
  localStorage.removeItem("user_token");
  return fetch(`${BASE_URL}/logout/`, {
    method: "POST",
    headers: { "Content-Type": "application/json", Authorization: token },
    body: JSON.stringify({ token: token }),
  });
}

function register(user) {
  const formData = new FormData();
  Object.keys(user).forEach((key) => {
    formData.append(key, user[`${key}`]);
  });
  const requestOptions = {
    method: "POST",
    body: formData,
  };

  return fetch(`${BASE_URL}/sign_up/`, requestOptions)
    .then((res) => res.json())
    .then((res) => {
      return res;
    });
}

function userList(filter) {
  const token = `Token ${JSON.parse(localStorage.getItem("user_token"))}`;
  const requestOptions = {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: token,
    },
    body: JSON.stringify(filter),
  };

  return fetch(`${BASE_URL}/user_detail_page/`, requestOptions)
    .then((res) => res.json())
    .then((res) => {
      return res;
    });
}

