import React from "react";
import { SERVICES } from "../data";
import styled from "styled-components";

const ServiceCard = styled.div`
  align-self: ${({ align }) => align};
  display: flex;
  width: 400px;
`;

const ServiceIconWrapper = styled.div`
  width: 60px;
  height: 60px;
  padding: 10px;
  box-shadow: 0px 3px 72px rgba(255, 71, 115, 0.15);
`;

const ServiceBody = styled.div`
  margin-left: 10px;
`;

const ServiceTitle = styled.h3`
  font-family: DM Sans;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0;
`;

const ServiceDescription = styled.h4`
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0;
`;

function Services() {
  return (
    <>
      {SERVICES.map((service, i) => (
        <ServiceCard
          key={service.id}
          align={i % 2 === 0 ? "flex-start" : "flex-end"}
        >
          {i % 2 === 0 && (
            <ServiceIconWrapper>
              <service.icon />
            </ServiceIconWrapper>
          )}
          <ServiceBody>
            <ServiceTitle>{service.title}</ServiceTitle>
            <ServiceDescription>{service.description}</ServiceDescription>
          </ServiceBody>
          {i % 2 !== 0 && (
            <ServiceIconWrapper>
              <service.icon />
            </ServiceIconWrapper>
          )}
        </ServiceCard>
      ))}
    </>
  );
}

export default Services;
