import React, { useCallback, useEffect } from "react";
import { Card } from "../../Common/components/Card/Card";
import styled from "styled-components";
import { PrimaryButton } from "../../Common/components/Buttons/PrimaryButton";
import Input from "../../Common/components/Input/Input";
import { LinkText } from "../../Common/components/LinkText/LinkText";
import Dropdown from "../../Common/components/Dropdown/Dropdown";

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const SignupButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Row = styled.div`
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 180px 180px;
`;

const BackLink = styled(LinkText)`
  text-align: center;
  margin-top: 10px;
`;

function ReligionDetails({
  handleContinue,
  values,
  handleBack,
  handleChange,
  errors,
  validateForm,
  setErrors,
}) {
  useEffect(() => {
    setErrors({});
  }, [setErrors]);
  const gotoNextStep = useCallback(async () => {
    const err = await validateForm();
    if (
      !(err.religion || err.caste || err.gotra || err.nakshatra || err.rashi)
    ) {
      handleContinue();
    }
  }, [handleContinue, validateForm]);
  return (
    <>
      <Card width={450}>
        <Title>Religion Details</Title>
        <InputWrapper>
          <Row>
            <Dropdown
              label="Religion"
              name="community"
              onChange={handleChange}
              value={values.community}
              options={["Hindu", "Muslim", "Christain"]}
              error={errors.community}
            />
            <Input
              label="Caste"
              name="caste"
              type="text"
              onChange={handleChange}
              value={values.caste}
              placeholder=""
              error={errors.caste}
            />
          </Row>
          <Row>
            <Input
              label="Gotra"
              type="text"
              name="gotra"
              onChange={handleChange}
              value={values.gotra}
              placeholder=""
              error={errors.gotra}
            />
            <Input
              label="Nakshatra"
              type="text"
              name="nakshatra"
              onChange={handleChange}
              value={values.nakshatra}
              placeholder=""
              error={errors.nakshatra}
            />
          </Row>
          <Row>
            <Input
              label="Rashi"
              type="text"
              name="rashi"
              onChange={handleChange}
              value={values.rashi}
              placeholder=""
              error={errors.rashi}
            />
          </Row>
          <SignupButton onClick={gotoNextStep}>Continue</SignupButton>
          <BackLink onClick={handleBack}>Back</BackLink>
        </InputWrapper>
      </Card>
    </>
  );
}

export default ReligionDetails;
