import React, { useEffect } from "react";
import { Card } from "../../Common/components/Card/Card";
import styled from "styled-components";
import { PrimaryButton } from "../../Common/components/Buttons/PrimaryButton";
import Input from "../../Common/components/Input/Input";
import { LinkText } from "../../Common/components/LinkText/LinkText";
import Radio from "../../Common/components/Radio/Radio";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import DateInput from "../../Common/components/DateInput/DateInput";
import { parseDate } from "../../Common/utils";

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const SignupButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Row = styled.div`
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 180px 180px;
`;

const BackLink = styled(LinkText)`
  text-align: center;
  margin-top: 10px;
`;

const GENDER_OPTIONS = [
  { id: "male", value: "Male" },
  { id: "female", value: "Female" },
];

function BasicDetails({
  handleContinue,
  handleChange,
  values,
  handleBack,
  setFieldValue,
  errors,
  validateForm,
  setErrors,
}) {
  // const handleDateChange = (date) => {
  //   const dateString = parseDate(date);
  //   setFieldValue("date_of_birth", dateString);
  // };
  useEffect(() => {
    setErrors({});
  }, [setErrors]);
  const gotoNextStep = async () => {
    const err = await validateForm();
    if (
      !(
        err.date_of_birth ||
        err.gender ||
        err.height ||
        err.weight ||
        err.highest_education ||
        err.address
      )
    ) {
      handleContinue();
    }
  };
  return (
    <>
      <Card width={450}>
        <Title>Basic Details</Title>
        <InputWrapper>
          <Row>
            <Input
              label="Date of Birth"
              type="text"
              name="date_of_birth"
              value={values.date_of_birth}
              placeholder="YYYY-MM-DD"
              onChange={handleChange}
              error={errors.date_of_birth}
            />
            {/* <DateInput
              placeholder="YYYY-MM-DD"
              value={values.date_of_birth}
              onChange={handleDateChange}
            /> */}
            <Radio
              label="Gender"
              options={GENDER_OPTIONS}
              selected={values.gender}
              name="gender"
              onChange={handleChange}
              error={errors.gender}
            />
          </Row>
          <Row>
            <Input
              label="Height (in ft, inch)"
              type="text"
              name="height"
              value={values.height}
              placeholder="Enter your height"
              onChange={handleChange}
              error={errors.height}
            />
            <Input
              label="Looking For"
              type="text"
              name="requirements"
              value={values.requirements}
              placeholder="Looking for"
              onChange={handleChange}
              error={errors.requirements}
            />
          </Row>
          <Row>
            <Input
              label="Education & Occupation"
              type="text"
              name="highest_education"
              value={values.highest_education}
              placeholder="Enter your Education & Occupation"
              onChange={handleChange}
              error={errors.highest_education}
            />
            <Input
              label="City"
              type="text"
              name="address"
              value={values.address}
              placeholder="Enter your city"
              onChange={handleChange}
              error={errors.address}
            />
          </Row>
          <SignupButton onClick={gotoNextStep}>Continue</SignupButton>
          <BackLink onClick={handleBack}>Back</BackLink>
        </InputWrapper>
      </Card>
    </>
  );
}

export default BasicDetails;
