const ProfileListWrapper = styled.div`
  align-self: left;
  width: 50%;
  min-width: 600px;
`;

const NoMatches = styled.p`
  color: #ff4773;
  text-align: center;
`;

const Filter = styled.div`
  display: flex;
`;

const FilterItem = styled.p`
  color: ${({ selected }) => (selected ? `#FF4773` : `rgba(38, 24, 10, 0.7)`)};
  margin: 10px;
  cursor: pointer;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
`;

function Profiles(props) {
  const [selectedFilter, setSelectedFilter] = useState("all");
  useEffect(() => {
    props.getUserList();
  }, [props]);
  const { matches: profiles, loadingUsers } = useSelector((state) => {
    return { ...state.authentication };
  });
  const handleFilter = (e) => {
    setSelectedFilter(e.target.id);
  };
  const filteredProfiles = useMemo(() => {
    if (profiles && profiles.length > 0) {
      if (selectedFilter === "all") {
        return profiles;
      } else if (selectedFilter === "brides") {
        return profiles.filter((profile) => profile.is_female);
      }
      return profiles.filter((profile) => profile.is_male);
    }
  }, [profiles, selectedFilter]);
  return (
    <Container>
      <ProfileListContainer>
        {filteredProfiles && (
          <Filter>
            <FilterItem
              onClick={handleFilter}
              selected={selectedFilter === "all"}
              id="all"
            >
              All
            </FilterItem>
            <FilterItem
              onClick={handleFilter}
              selected={selectedFilter === "brides"}
              id="brides"
            >
              Brides
            </FilterItem>
            <FilterItem
              onClick={handleFilter}
              selected={selectedFilter === "grooms"}
              id="grooms"
            >
              Grooms
            </FilterItem>
          </Filter>
        )}
        {loadingUsers && !(filteredProfiles && filteredProfiles.length) ? (
          <Loader>
            <div class="loader">Loading...</div>
          </Loader>
        ) : filteredProfiles && filteredProfiles.length > 0 ? (
          <ProfileListWrapper>
            {filteredProfiles.map((profile) => (
              <ProfileCard key={profile.id} {...profile} />
            ))}
          </ProfileListWrapper>
        ) : (
          !loadingUsers && <NoMatches>No profiles found.</NoMatches>
        )}
      </ProfileListContainer>
    </Container>
  );
}

const mapDispatchToProps = {
  getUserList: userActions.getUserList,
};

export default connect(null, mapDispatchToProps)(Profiles);
