import React, { useEffect } from "react";
import styled from "styled-components";
import { useFileSelect } from "../../hooks/useFileSelect";

const Container = styled.div`
  width: 100%;
  position: relative;
`;

const Label = styled.label`
  font-size: 12px;
`;

const FileWrapper = styled.label`
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  background: rgba(255, 237, 241, 1);
  ${({ hasError }) => hasError && `border: 1px solid red`};
`;

const FileBrowse = styled.input.attrs({ type: "file", id: "file-upload" })`
  display: none;
`;

const FileName = styled.h4``;

const Text = styled.h4`
  font-size: 12px;
  font-weight: 400;
`;

const Error = styled.label`
  color: red;
  font-size: 10px;
  position: absolute;
  bottom: 24px;
  right: 135px;
`;

function FileUpload({ label, name, onChange, error }) {
  const [selectedFile, preview, isImage, handleFileChange] = useFileSelect();
  useEffect(() => {
    selectedFile && onChange(selectedFile);
  }, [selectedFile]);
  return (
    <Container>
      <Label>{label}</Label>
      <FileBrowse onChange={handleFileChange} />
      <FileWrapper hasError={error} htmlFor="file-upload">
        {(preview || (selectedFile && selectedFile.name)) && (
          <FileName>
            {!isImage ? preview : selectedFile && selectedFile.name}
          </FileName>
        )}
        <Text>
          {preview || (selectedFile && selectedFile.name)
            ? "Change File"
            : "Choose File"}
        </Text>
      </FileWrapper>
      {error && <Error>{error}</Error>}
    </Container>
  );
}

export default FileUpload;
