import React from "react";
import styled from "styled-components";

const Container = styled.div``;

const RadioInput = styled.input.attrs({ type: "radio" })`
  display: none;
`;

const Indicator = styled.div`
  height: 20px;
  width: 20px;
  border-radius: 50%;
  padding: 2px;
  border: 2px solid #ff4773;
  ${({ selected }) =>
    selected &&
    `background: #ff4773;
    box-shadow: 0px 0px 0px 2px #fff inset;
  `}
`;

const Label = styled.label`
  cursor: pointer;
  margin-left: 5px;
  font-size: 13px;
`;

const TextLabel = styled.label`
  font-size: 12px;
  margin-bottom: 6px;
`;

const Option = styled.label`
  cursor: pointer;
  display: flex;
  margin-right: 12px;
`;

const Wrapper = styled.div`
  display: flex;
  margin-top: 6px;
  align-items: center;
`;

function Radio({ options, selected, name, onChange, label }) {
  const handleChange = (e) => {
    onChange && onChange(e);
  };
  return (
    <Container>
      <TextLabel>{label}</TextLabel>
      <Wrapper>
        {options.map(({ id, value }) => {
          return (
            <Option key={id} htmlFor={`radio_${id}`}>
              <RadioInput
                onChange={handleChange}
                id={`radio_${id}`}
                name={name}
                value={value}
                checked={value === selected}
              />
              <Indicator
                htmlFor={`radio_${id}`}
                selected={value === selected}
              />
              <Label htmlFor={`radio_${id}`}>{value}</Label>
            </Option>
          );
        })}
      </Wrapper>
    </Container>
  );
}

export default Radio;
