import { userConstants } from "../constant";

let user = JSON.parse(localStorage.getItem("user"));
const initialState = user ? { loggedIn: true, user: {}, matches: [] } : {};

export function authentication(state = initialState, action) {
  switch (action.type) {
    case userConstants.LOGIN_REQUEST:
      return {
        loggingIn: true,
        user: action.user,
      };
    case userConstants.LOGIN_SUCCESS:
      return {
        loggedIn: true,
        loggingIn: false,
        user: action.user.data[0],
      };
    case userConstants.LOGIN_FAILURE:
      return {};
    case userConstants.USERLIST_REQUEST:
      return {
        ...state,
        loadingUsers: true,
      };
    case userConstants.USERLIST_SUCCESS:
      return {
        ...state,
        loadingUsers: false,
        matches: action.users.data,
      };
    case userConstants.USERLIST_FAILURE:
      return {};
    case userConstants.APPROVE_REQUEST:
      return {
        ...state,
        approving: action.userId,
      };
    case userConstants.APPROVE_SUCCESS:
      return { ...state, approving: null };
    case userConstants.APPROVE_FAILURE:
      return { ...state };
    case userConstants.REJECT_REQUEST:
      return {
        ...state,
        rejecting: action.userId,
      };
    case userConstants.REJECT_SUCCESS:
      return { ...state, rejecting: null };
    case userConstants.REJECT_FAILURE:
      return { ...state, rejecting: null };
    case userConstants.FORGOT_PASSWORD_REQUEST:
      return {
        resetting: true,
      };
    case userConstants.FORGOT_PASSWORD_SUCCESS:
      return {
        resetting: false,
      };
    case userConstants.FORGOT_PASSWORD_FAILURE:
      return {
        resetting: false,
      };
    case userConstants.LOGOUT:
      return {};
    default:
      return state;
  }
}
