import React, { useEffect } from "react";
import styled from "styled-components";
import { PrimaryButton } from "../Common/components/Buttons/PrimaryButton";
import Input from "../Common/components/Input/Input";
import { ReactComponent as Lock } from "../Common/assets/Icon/lock.svg";
import { ReactComponent as Phone } from "../Common/assets/Icon/phone.svg";
import { LinkText } from "../Common/components/LinkText/LinkText";
import { Card } from "../Common/components/Card/Card";
import { connect } from "react-redux";
import { userActions } from "../redux/Login/actions";
import { withFormik } from "formik";
import { useSelector } from "react-redux";
import * as Yup from "yup";
import { toast } from "react-toastify";

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const LoginButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const Text = styled.h3`
  text-align: center;
  font-weight: 400;
  font-size: 13px;
`;

function Login({
  values,
  handleSubmit,
  handleChange,
  history,
  errors,
  logout,
}) {
  useEffect(() => {
    let user = localStorage.getItem("user");
    let token = localStorage.getItem("user_token");
    if (user && token) {
      user.is_admin
        ? history.push("/admin/profiles")
        : history.push("/matches");
    } else {
      logout(
        () => {
          history.push("/login");
        },
        (err) => {}
      );
    }
  }, [history, logout]);
  const loggingIn = useSelector((state) => {
    return state.authentication.loggingIn;
  });
  return (
    <Card width={400}>
      <Title>Login</Title>
      <InputWrapper>
        <Input
          label="Phone"
          type="text"
          name="phone_no"
          onChange={handleChange}
          value={values.phone_no}
          placeholder="Enter phone number"
          icon={<Phone />}
          error={errors.phone_no}
        />
        <Input
          label="Password"
          type="password"
          name="password"
          value={values.password}
          onChange={handleChange}
          placeholder="Enter password"
          icon={<Lock />}
          error={errors.password}
        />
        <LoginButton isLoading={loggingIn} onClick={handleSubmit}>
          Login
        </LoginButton>
        <Text>
          Dont have an account? <LinkText to="/signup">Signup</LinkText>
        </Text>
      </InputWrapper>
    </Card>
  );
}

const LoginSchema = Yup.object().shape({
  phone_no: Yup.string()
    .required("Phone Number is required")
    .matches(/^[0-9]+$/, "Must be only digits")
    .min(10, "Must be exactly 10 digits")
    .max(10, "Must be exactly 10 digits"),
  password: Yup.string().required("Password is required"),
});

const LoginFormik = withFormik({
  mapPropsToValues: () => ({ phone_no: "", password: "" }),
  validationSchema: LoginSchema,
  handleSubmit: (values, { props }) => {
    props.login(
      values.phone_no,
      values.password,
      (user) => {
        if (user.data[0].is_admin) {
          props.history.push("/admin/profiles");
        } else {
          props.history.push("/matches");
        }
      },
      (err) => {
        toast.error("Login failed - " + err, {
          position: "bottom-right",
        });
      }
    );
  },
})(Login);

const mapDispatchToProps = {
  login: userActions.login,
  logout: userActions.logout,
};

const ConnectedLoginFormik = connect(null, mapDispatchToProps)(LoginFormik);

export default ConnectedLoginFormik;
