import React, { useEffect } from "react";
import ProfileCompletion from "./components/ProfileCompletion";
import { withFormik } from "formik";
import { connect } from "react-redux";
import { userActions } from "../redux/Login/actions";
import Credentials from "./components/Credentials";
import { useState } from "react";
import BasicDetails from "./components/BasicDetails";
import ReligionDetails from "./components/ReligionDetails";
import ProfileInformation from "./components/ProfileInformation";
import * as Yup from "yup";
import { toast } from "react-toastify";

const STEP_OPTIONS = {
  CREDENTIALS: "CREDENTIALS",
  BASIC_DETAILS: "BASIC_DETAILS",
  RELIGION_DETAILS: "RELIGION_DETAILS",
  PROFILE_INFORMATION: "PROFILE_INFORMATION",
};

const STEPS = [
  {
    name: "CREDENTIALS",
    completion: 0,
  },
  {
    name: "BASIC_DETAILS",
    completion: 25,
  },
  {
    name: "RELIGION_DETAILS",
    completion: 60,
  },
  {
    name: "PROFILE_INFORMATION",
    completion: 80,
  },
];

function Signup({
  handleSubmit,
  values,
  handleChange,
  setFieldValue,
  validateForm,
  errors,
  setErrors,
  history,
  logout,
}) {
  useEffect(() => {
    let user = localStorage.getItem("user");
    let token = localStorage.getItem("user_token");
    if (user && token) {
      user.is_admin
        ? history.push("/admin/profiles")
        : history.push("/matches");
    } else {
      logout(
        () => {
          history.push("/signup");
        },
        (err) => {}
      );
    }
  }, [history, logout]);
  const [currentStep, setCurrentStep] = useState(STEPS[0]);
  const handleContinue = () => {
    let currentIndex = STEPS.findIndex(
      (step) => step.name === currentStep.name
    );
    setCurrentStep(STEPS[currentIndex + 1]);
  };
  const handleBack = () => {
    let currentIndex = STEPS.findIndex(
      (step) => step.name === currentStep.name
    );
    setCurrentStep(STEPS[currentIndex - 1]);
  };
  const handleFileChange = (key, file) => {
    setFieldValue(key, file);
  };
  return (
    <>
      <ProfileCompletion progress={currentStep.completion} />
      {currentStep.name === STEP_OPTIONS.CREDENTIALS ? (
        <Credentials
          errors={errors}
          validateForm={validateForm}
          values={values}
          handleChange={handleChange}
          handleContinue={handleContinue}
          setErrors={setErrors}
        />
      ) : currentStep.name === STEP_OPTIONS.BASIC_DETAILS ? (
        <BasicDetails
          errors={errors}
          validateForm={validateForm}
          values={values}
          setFieldValue={setFieldValue}
          handleChange={handleChange}
          handleContinue={handleContinue}
          handleBack={handleBack}
          setErrors={setErrors}
        />
      ) : currentStep.name === STEP_OPTIONS.RELIGION_DETAILS ? (
        <ReligionDetails
          errors={errors}
          validateForm={validateForm}
          values={values}
          handleChange={handleChange}
          handleContinue={handleContinue}
          handleBack={handleBack}
          setErrors={setErrors}
        />
      ) : currentStep.name === STEP_OPTIONS.PROFILE_INFORMATION ? (
        <ProfileInformation
          errors={errors}
          validateForm={validateForm}
          handleChange={handleFileChange}
          handleSubmit={handleSubmit}
          handleBack={handleBack}
          setErrors={setErrors}
        />
      ) : null}
    </>
  );
}

const SignupSchema = Yup.object().shape({
  first_name: Yup.string().required("First Name is required"),
  father_name: Yup.string().required("Fathers Name is required"),
  phone_no: Yup.string()
    .required("Phone Number is Required")
    .matches(/^[0-9]+$/, "Must be only digits")
    .min(10, "Must be exactly 10 digits")
    .max(10, "Must be exactly 10 digits"),
  password: Yup.string().required("Password is Required"),
  date_of_birth: Yup.date().required("Required"),
  height: Yup.string().required("Height is Required"),
  requirements: Yup.string().required("Looking for is Required"),
  highest_education: Yup.string().required("Education is Required"),
  gender: Yup.string().required("Gender is Required"),
  address: Yup.string().required("Address is Required"),
  community: Yup.string().required("Religion is Required"),
  caste: Yup.string().required("Caste is Required"),
  gotra: Yup.string().required("Gotra is Required"),
  nakshatra: Yup.string().required("Nakshatra is Required"),
  rashi: Yup.string().required("Rashi is Required"),
  photo1: Yup.mixed().required("Photo is Required"),
  horoscope: Yup.mixed().required("Horoscope is Required"),
});

const SignupFormik = withFormik({
  mapPropsToValues: () => ({
    hav_id: "JSB" + Math.floor(100000 + Math.random() * 900000),
    first_name: "",
    father_name: "",
    phone_no: "",
    password: "",
    gender: "Male",
    is_user: true,
    is_admin: false,
  }),
  validationSchema: SignupSchema,
  handleSubmit: (values, { props }) => {
    const is_female = values.gender === "Female";
    const formValues = {
      ...values,
      is_female,
      is_male: !is_female,
      email: "",
      occupation: "",
    };
    props.register(
      formValues,
      () => {
        toast.success(
          "`Thank you for Signing Up. Your profile will be verified and activated withing 24 hours. Please contact 8792737236 in case of any clarification or question.`  - TSS Jeevan Saathi",
          {
            position: "bottom-right",
          }
        );
        props.history.push("/login");
      },
      (err) => {
        toast.error("Signup failed !!!", {
          position: "bottom-right",
        });
      }
    );
  },
})(Signup);

const mapDispatchToProps = {
  register: userActions.register,
  logout: userActions.logout,
};

const ConnectedSignupFormik = connect(null, mapDispatchToProps)(SignupFormik);

export default ConnectedSignupFormik;
