import React from "react";
import styled from "styled-components";
import { ReactComponent as UserAdd } from "../../Common/assets/Icon/user-add.svg";
import { ReactComponent as UserSetting } from "../../Common/assets/Icon/user-setting.svg";
import { ReactComponent as Logout } from "../../Common/assets/Icon/logout.svg";
import { withRouter } from "react-router";
import { Link } from "react-router-dom";

const Container = styled.div`
  width: 250px;
  position: fixed;
  padding: 20px;
  height: 100vh;
  -webkit-box-shadow: 2px 0px 58px rgba(255, 71, 115, 0.1);
  -moz-box-shadow: 2px 0px 58px rgba(255, 71, 115, 0.1);
  box-shadow: 2px 0px 58px rgba(255, 71, 115, 0.1);
`;

const Header = styled.div``;

const MenuItem = styled(Link)`
  text-decoration: none;
  padding: 6px 14px;
  margin: 10px;
  cursor: pointer;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  border-radius: 8px;
  ${({ isActive }) =>
    isActive ? `color: #ffffff; background: #ff4773;` : `color: #26180a;`}
`;

const MenuText = styled.h3`
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
`;

const MenuIcon = styled.div`
  height: 20px;
  width: 20px;
  margin-right: 10px;
`;

const MENUS = [
  {
    id: "1",
    name: "Profile Requests",
    icon: UserAdd,
    path: "/admin/profiles",
  },
  {
    id: "2",
    name: "Profile Setting",
    icon: UserSetting,
    path: "/admin/profile-setting",
  },
  // {
  //   id: "3",
  //   name: "Logout",
  //   icon: Logout,
  //   path: "/admin/logout",
  // },
];

function LeftMenu({ location }) {
  return (
    <Container>
      <Header></Header>
      {MENUS.map((menu) => (
        <MenuItem
          to={menu.path}
          id={menu.path}
          key={menu.id}
          isActive={location.pathname.includes(menu.path)}
        >
          <MenuIcon>
            <menu.icon />
          </MenuIcon>
          <MenuText>{menu.name}</MenuText>
        </MenuItem>
      ))}
    </Container>
  );
}

export default withRouter(LeftMenu);
